body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  letter-spacing: 0.025em;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    'PingFang TC', 'Heiti TC', '\5FAE\8EDF\6B63\9ED1\9AD4', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  line-height: 1.5em;
}

.ui.button {
  margin: 0;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.Quiz {
  background: repeating-linear-gradient(-10deg, hsla(50, 100%, 80%, 0.2),  hsla(50, 80%, 100%, 1),  hsla(50, 100%, 80%, 0.1));
}

.Quiz .main {
  position: relative;
  flex-grow: 1;
  display: flex;
}

.Quiz .main .floor,
.Quiz .main .roof {
  padding: 2rem 0;
  transition: all 0.5s ease-out;
}

.Quiz .main .floor {
  flex-grow: 1;
}

.Quiz .main .floor.is-blurred {
  -webkit-filter: blur(1rem);
          filter: blur(1rem);
}

.Quiz .main .roof {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsla(0,0%,100%,0.5);
  overflow-y: auto;
}

.Quiz .content-wrapper {
  max-width: 720px;
  margin: 0 auto;
}

.Quiz .img-positioner,
.Quiz .text-positioner {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Quiz .img-holder {
  height: 15rem;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  -webkit-animation: 1s ease-out 0s 1 fadeIn;
          animation: 1s ease-out 0s 1 fadeIn;
}

@media only screen and (min-width: 768px) {
.Quiz .img-wrapper {
  order: 2;
}
.Quiz .text-positioner {
  padding: 1rem 0;
}
}

@media only screen and (max-width: 767px) {
.Quiz .content-wrapper {
  max-width: 480px;
}
}
.Answer .header {
  margin: 1rem 0;
}

.Answer .block.header {
  background-color: #fff;
  display: inline-block;
  padding-left: 2em;
  padding-right: 1em;
  position: relative;
}

.Answer .header .corner.label {
  margin: 0;
}

.Answer .header .corner.label .icon {
  opacity: 1;
}

.Answer .teal.header {
  border: 1px #00b5ad solid;
}

.Answer .pink.header {
  border: 1px #e03997 solid;
}

.Complete {
}

.Progress .ui.progress {
  border-radius: 0;
  margin: 0;
}

.Progress .ui.progress .bar {
  height: 4px;
  border-radius: 0;
  transition: width 0.25s ease-out;
}


.Welcome {
  background: repeating-linear-gradient(-10deg, hsla(180, 100%, 80%, 0.2),  hsla(180, 80%, 100%, 1),  hsla(180, 100%, 80%, 0.1));
}

.Welcome .main {
  flex-grow: 1;
  padding: 2rem 0;
}

.Welcome .content-wrapper {
  margin: 0 auto;
}

.Welcome .img-positioner {
  height: 100%;
  display: flex;
  flex-direction: column-reverse;
}

.Welcome .img-holder {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  -webkit-animation: 1s ease-out 0s 1 fadeIn;
          animation: 1s ease-out 0s 1 fadeIn;
}

.Welcome .text-positioner {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Welcome .Title {
  border-image: linear-gradient(90deg, hsla(180, 50%, 50%, 0), hsla(180, 50%, 50%, 0.75), hsla(180, 50%, 50%, 0.75), hsla(180, 50%, 50%, 0)) 100% 1; 
}

@media only screen and (min-width: 768px) {
.Welcome .img-positioner {
  margin-right: -2rem;
}
.Welcome .img-holder {
  height: 100%;
}
.Welcome .text-positioner>.header {
  margin-top: 1.5rem;
}
}

@media only screen and (max-width: 767px) {
.Welcome .content-wrapper {
  max-width: 480px;
}
.Welcome .img-positioner {
  margin-right: 0;
}
.Welcome .img-holder {
  height: 15rem;
}
}
.Map {
  -webkit-animation: 1s ease-out 0s 1 fadeIn;
          animation: 1s ease-out 0s 1 fadeIn;
}

.Map .group {
  fill: hsla(180, 50%, 50%, 1);
  stroke-width: 1px;
  stroke: hsla(0, 0%, 100%, 1);
  cursor: pointer;
  transition: all 0.25s ease-out;
}

.Map .group:hover,
.Map .group:focus,
.Map .group:active {
  fill: hsla(50, 100%, 50%, 1);
  stroke: hsla(50, 100%, 50%, 1);
  cursor: pointer;
}

.Portal {
  background: repeating-linear-gradient(-10deg, hsla(180, 100%, 80%, 0.2),  hsla(180, 80%, 100%, 1),  hsla(180, 100%, 80%, 0.1));
}

.Portal .main {
  position: relative;
  flex-grow: 1;
  display: flex;
}

.Portal .main .floor,
.Portal .main .roof {
  padding: 2rem 0;
  transition: all 0.5s ease-out;
}

.Portal .main .floor {
  flex-grow: 1;
}

.Portal .main .floor.is-blurred {
  -webkit-filter: blur(1rem);
          filter: blur(1rem);
}

.Portal .main .roof {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsla(0,0%,100%,0.5);
}

.Report {
  background: repeating-linear-gradient(-10deg, hsla(180, 100%, 80%, 0.2),  hsla(180, 80%, 100%, 1),  hsla(180, 100%, 80%, 0.1));
}

.Report .main {
  flex-grow: 1;
  padding: 2rem 0;
}

.Report .content-wrapper {
  max-width: 720px;
  margin: 0 auto;
}

.Report .image {
  line-height: 0;
}

.Report svg path {
  fill: hsla(180, 50%, 50%, 0.2);
  stroke-width: 1px;
  stroke: hsla(0, 0%, 100%, 1);
}

.Report svg .active path {
  fill: hsla(180, 50%, 50%, 1);
}

.Report .ui.cards>.card>.content>a.header:hover {
  color: hsla(180, 50%, 50%, 1);
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: hsla(0,0%,0%,0.05);
}

.App .Header,
.App .Footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.App .Header {
  background: #fff;
  box-shadow: 0 1px 1px 0 hsla(0,0%,0%,0.1);
}

.App .Footer {
  background: hsla(0,0%,100%,0.5);
  border-top: 1px #fff solid;
  box-shadow: 0 -1px 1px 0 hsla(0,0%,0%,0.1);
}

.App .Header .grid,
.App .Footer .grid {
  display: grid;
  grid-column-gap: 1rem;
  align-items: center;
}

.App .Header .grid {
  grid-template-areas: 'logo title';
  grid-template-columns: 3rem auto 3rem;
  padding: 1rem 0;
}

.App .Header .grid .logo {
  grid-area: logo;
}

.App .Header .grid .title {
  grid-area: title;
}

.App .Footer .grid {
  grid-template-areas: '. banner source';
  grid-template-columns: 3rem auto 3rem;
}

.App .Footer .grid .banner {
  grid-area: banner;
  width: 15rem;
  justify-self: center;
}

.App .Footer .grid .source {
  grid-area: source;
}

.App .Body {
  flex-grow: 1;
  flex-shrink: 1;
  display: flex;
}

.App .Body .route {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.App a,
.App a:link {
  color: hsla(180, 50%, 50%, 1);
}

.Title {
  letter-spacing: 0.1em;
  border-radius: 20rem 1rem / 1rem 20rem;
}

.Dialog {
  border-radius: 5rem 2rem / 2rem 5rem;
  padding: 1rem;
  margin: 2rem 0;
}

.Dialog.teal {
  color: hsla(180, 50%, 25%, 1);
  background-color: hsla(180, 50%, 60%, 0.1);
  box-shadow: 0 0 0 4px hsla(180, 50%, 60%, 0.1);
  border: 4px hsla(180, 50%, 50%, 0.25) dotted;
}

.Dialog.pink {
  color: hsla(330, 50%, 25%, 1);
  background-color: hsla(330, 50%, 60%, 0.1);
  box-shadow: 0 0 0 4px hsla(330, 50%, 60%, 0.1);
  border: 4px hsla(330, 50%, 50%, 0.25) dotted;
}

